
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XHeader
},
data() {
return {
username: '',
telephone: '17630378060',
password: '',
repassword: '',
code: '',
codeText: '',
sending: false,
menus: {
menu1: 'Take Photo',
menu2: 'Choose from photos'
},
showMenus: false
}
},
created(){
this.getAllCompany();
},
methods: {
getAllCompany() {
this.Tools.ajax({
method: '/findByUserId',
data: {
Authorization : localStorage.getItem("prevToken")
}
}).then(res => {
console.log(res)
switch (res.status) {
case '0':

this.$vux.toast.text('');
break;
case '2':
this.$vux.toast.text('2');
break;
default:

this.$vux.toast.text('');
break;
}
});
},
register() {
console.log(666, this)
if (!this.username) {
this.$vux.toast.text('');
return false;
}
if (!this.Tools.RegExp.Phone.test(this.telephone)) {
this.$vux.toast.text('');
return false;
}
if (!this.Tools.RegExp.password.test(this.password)) {
this.$vux.toast.text('4~20 _');
return false;
}
if (this.password !== this.repassword) {
this.$vux.toast.text('');
return false;
}
if (!this.code) {
this.$vux.toast.text('');
return false;
}

this.Tools.ajax({
method: '/register',
headers : {
'Content-type': 'application/json'
},
data: {
"username": this.username.trim(),
"telephone": this.telephone.trim(),
"passwd": md5(this.password.trim()),
"code": this.code.trim()
}
}).then(res => {
switch (res.status) {
case '0':

this.$vux.toast.text('');
setTimeout(() => {
this.$router.goBack();
}, 1500);
break;
case '2':
this.$vux.toast.text('');
break;
case '3':
this.$vux.toast.text('');
break;
case '4':
this.$vux.toast.text('');
break;
default:

this.$vux.toast.text('');
break;
}
});

},
clearPhone() {
this.telephone = '';
},
clearPassword() {
this.password = '';
},
}
}
